body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
  text-align: center;
}

.profile-img{
  margin-top: 30px;
  border-radius: 50px;
  height: 100px;
  width: 100px;
}

.top-title{
  font-size:2.5rem;
  font-weight: 800;
  letter-spacing: 3px;
}
.job-title{
  font-weight: 500;
  letter-spacing: 2px;
}

.job-title::after{
  content: " ";
  border: solid 1px #0093cb;
  display: block;
  width: 55px;
  margin: 15px auto;
}

.section-title{
  font-size:2.3rem;
  font-weight:500;
  letter-spacing: 2px;
  margin-top:30px;
  margin-bottom:30px;
}

.sns-icon{
  margin:10px;
}

.product-card{
  margin:10px;
  width:345px;
  height:400px;
}
.product-img{
  height:200px;
}

.card-container{
  margin:30px;
  display:flex;
  justify-content: center;
  align-items: center;
}

.skills-chart{
  display:inline-block;
  text-align: center;
}
